import noData from '@/components/noData'
import back from '@/components/back'
import { introAdmin } from '../../config'
export default {
    components:{
        noData,
        back,
    },
    data(){
        return{
			user: this.$store.state.userInfo,
			page: 1,
			haveMore: true,
			search:'',
			list:[],
			picArr:[],
        }
    },
    created(){
        this.getTReportPage()
    },
    mounted(){
    },
    beforeRouteLeave(to, from, next) {
    	window.onscroll = null
    	if(from.meta.keepAlive) {　
    		from.meta.scollTopPosition = this.scrollTopDistance;
    	}
    	next()
    },
    beforeRouteEnter(to, from, next) { //进入前
    	next(vm => {
    		vm.getMore()
    		setTimeout(() => {
    			vm.$refs.el.scrollTop = to.meta.scollTopPosition;
    		}, 1)
    	});
    },
    beforeDestroy() {
    	window.onscroll = null
    	this.idx = null
    },
    methods: {
		getTReportPage(h){
            if(this.haveMore){
               this.$post('MDTAPI/MDT/QueryDoctorMDTReportPage',{
                   DrId:this.user.Data.User.DoctorId,
                   CompleteStat:-1,
                   PageIndex:this.page,
                   PageSize:15,
               }).then(o => {
               	 if (!o.ReturnData || o.ReturnData.length < 15){
               	 	this.haveMore = false
               	 }
               	 else {
               	 	this.haveMore = true
               	 }
               	 if (h){
               	 	o.ReturnData.forEach(v => {
               	 		this.list.push(v)
               	 	})
               	 }
               	 else {
               	 	this.list = o.ReturnData
               	 }
               })
            }
		},
        toGroupChat(i){
             this.$bridge.callhandler('toGroupChat', i.IMGroupId)
        },
        toMyReport(i){
            this.$bridge.callhandler('toMyReport', i.SubjectBuyRecordId)
        },
        getMore(){
            const el = this.$refs.el
            let timer = null
            window.onscroll = () => {
                clearTimeout(timer)
                timer = setTimeout(() => {
        			const top = document.documentElement.scrollTop
        				|| window.pageYOffset
        				|| document.body.scrollTop,
        			h = el.scrollHeight,
        			wh = window.innerHeight
        			this.scrollTopDistance = top;
                    if (h - top - wh < 10 && this.haveMore){
                        this.page += 1
        				this.getTReportPage(true)
                    }
                }, 100)
            }
        },

    }
}



